<template>
  <div id="movie">
    <nav-bar>
      <span slot="leftSpan">Movies</span>
      <input
        slot="center"
        class="search"
        type="text"
        placeholder="ÁîüÊ¥ªÂ∞±ÂÉè‰∏ÄÁõíÂ∑ßÂÖãÂäõ,‰Ω†Ê∞∏Ëøú‰∏çÁü•ÈÅì‰Ω†‰ºöÂæóÂà∞‰ªÄ‰πà"
        v-model="inputValue"
        @keyup.enter="addItem"
      />
      <span slot="rightSpan">üé¨</span>
    </nav-bar>
    <task-bar>
      <is-read
        slot="tagging"
        :todo="todo"
        @reClick="change"
        :todoWord="[
          'Êú™Áúã' + '(' + todoCount + ')',
          'Â∑≤Áúã' + '(' + doneCount + ')',
        ]"
      ></is-read>
      <span v-if="!todoCount && todo">Âø´Êù•Ê∑ªÂä†‰Ω†ÊÉ≥ÁúãÁöÑÁîµÂΩ±Âêß</span>
      <span v-if="!doneCount && !todo">‰Ω†ËøòÊ≤°ÊúâÁúãÂÆåÁöÑÁîµÂΩ±Âì¶</span>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in myMovie"
          v-show="todo && !item.done"
          :item="item.title"
          :done="item.done"
          :key="item.tid"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in myMovie"
          v-show="!todo && item.done"
          :item="item.title"
          :done="item.done"
          :key="item.tid"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
    </task-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import ListItem from "components/content/listItem/ListItem.vue";
import IsRead from "components/common/isRead/IsRead.vue";

import { getData, saveData, deleteData } from "assets/js/todoList.js";

export default {
  name: "Movie",
  components: {
    NavBar,
    TaskBar,
    ListItem,
    IsRead,
  },
  data() {
    return {
      todo: true,
      myMovie: [
        // {title: 'üçéÂêÉËãπÊûú', done: true},
        // {title: 'üçìÂêÉËçâËéì', done: false},
        // {title: 'üçåÂêÉÈ¶ôËïâ', done: true},
        // {title: 'üçàÂêÉÊ©òÂ≠ê', done: false},
        // {title: 'üçâÂêÉË•øÁìú', done: false},
      ],
      inputValue: "",
    };
  },
  computed: {
    //Êú™ÂÆåÊàêÊï∞Èáè
    todoCount() {
      return this.myMovie.filter((item) => {
        return !item.done;
      }).length;
    },

    //Â∑≤ÂÆåÊàêÊï∞Èáè
    doneCount() {
      return this.myMovie.filter((item) => {
        return item.done;
      }).length;
    },
  },
  // È°µÈù¢Âä†ËΩΩÂç≥Ê∏≤ÊüìÊâÄÊúâÊï∞ÊçÆ
  created() {
    let allList = getData("myMovie");
    this.myMovie = allList;
  },
  watch: {
    myMovie: {
      handler() {
        saveData(this.myMovie, "myMovie");
      },
      deep: true, //Ê∑±ÁõëÂê¨
    },
  },
  methods: {
    addItem(e) {
      let data = e.target.value;
      if (data === "") {
        return alert("ËØ∑ËæìÂÖ•ÁîµÂΩ±ÂêçÁß∞");
      }
      this.myMovie.push({
        title: data,
        done: false,
        tid: new Date().valueOf(),
      });
      this.inputValue = "";
    },
    change() {
      this.todo = !this.todo;
    },
    deleteItem(index) {
      this.myMovie.splice(index, 1);
    },
    isDone(index) {
      this.myMovie[index].done = !this.myMovie[index].done;
    },
  },
};
</script>

